<template>
  <div class="f_bg">
    <nav-bar class="f_nav">
      <div slot="center">发现</div>
      <div slot="right" class="f_right">
        <div @click="toSearch">
          <img src="@/assets/img/find/search.png">
        </div>
      </div>
    </nav-bar>
    <TabControl :titles="classification" @tabClick="tabClick" ref="tabControl1" class="tab-control"
                v-show="isTabFixed"></TabControl>
    <div class="f_body">
      <BetterScroll class="content" ref="scroll" :probe-type="3" @scroll="contentScroll" :pull-up-load="true"
                    @pullingUp="loadMore">
        <el-card class="f_card">
          <img src="@/assets/img/find/f_baby.png" @load="imageLoad">
        </el-card>
        <TabControl :titles="classification" @tabClick="tabClick" ref="tabControl2"></TabControl>
        <div>
          <goods-list :goods="goods.records"/>
          <el-empty v-show="goodsLength === 0" description="暂无文章给你看哦"></el-empty>
        </div>
      </BetterScroll>
    </div>
    <BackTop @click.native="backTop" v-show="isShow"/>
  </div>
</template>

<script>
import TabControl from "@/components/tabControl/TabControl";
import NavBar from "@/components/navbar/NavBar";
import GoodsList from "@/components/goods/GoodsList";
import BetterScroll from "@/components/betterScroll/BetterScroll";
import BackTop from "@/components/backTop/BackTop";

export default {
  name: "Find",
  data() {
    return {
      goods: {},
      goodsLength: {},
      num:0,
      isShow: false,
      tabOffsetTop: 0,
      isLoad: false,
      isTabFixed: false,

      classification: [],
      classificationId: [],
      articleClassification: "推荐"
    }
  },
  created() {
    this.getClassification()
    this.$nextTick(() => {
      this.getArticle()
    })
  },
  methods: {
    //获取分类
    getClassification() {
      this.$http.get('classification').then(res => {
        for (var i = 0; i < res.data.data.length; i++) {
          this.classification[i] = res.data.data[i].name
          this.classificationId[i] = res.data.data[i].id
        }
        this.$set(this.classification)
        this.$set(this.classificationId)
      }).catch(err => err)
    },
    //获取文章
    getArticle() {
      this.$http.get('article?classification=' + this.articleClassification).then(res => {
        this.goods = res.data.data
        this.goodsLength = res.data.data.records.length
      }).catch(err => err)
    },
    tabClick(index) {
      if ((index + 1) === this.classificationId[index]) {
        this.articleClassification = this.classification[index]
        this.getArticle();
      }
      this.$refs.tabControl1.currentIndex = index;
      this.$refs.tabControl2.currentIndex = index;
    },
    backTop() {
      this.$refs.scroll.scrollTo(0, 0)
    },
    contentScroll(position) {
      this.isShow = (-position.y) > 240

      this.isTabFixed = (-position.y) > this.tabOffsetTop
    },
    loadMore() {
      // this.getHomeGoods(this.currentType)
      this.$refs.scroll.refresh()
    },
    imageLoad() {
      if (!this.isLoad) {
        this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop
        this.isLoad = true
      }
    },
  //  前往搜索页面
    toSearch(){
      this.$router.push('/search')
    }
  },
  components: {
    NavBar,
    TabControl,
    GoodsList,
    BetterScroll,
    BackTop
  }
}
</script>

