<template>
  <div class="tab_control">
    <div v-for="(item, index) in titles" class="tab_control_item" :class="{active: index === currentIndex}" @click="itemClick(index)">
      <span>{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "TabControl",
  data() {
    return {
      currentIndex:0
    }
  },
  created() {
  },
  methods: {
    itemClick(index){
      this.currentIndex = index;
      this.$emit('tabClick',index)
    }
  },
  props: {
    titles: {
      type: Array,
      default() {
        return []
      }
    }
  }
}
</script>

<style scoped>
.tab_control {
  display: flex;
  text-align: center;
  font-size: 15px;
  height: 40px;
  line-height: 40px;
  font-weight: bold;
  color: #333333;
}

.tab_control_item {
  flex: 1;
}
.active{
  font-size: 20px;
color: black;
}
.active::after {
  content: '';
  width: 50%;
  display: block;
  margin: -6px auto;
  border-bottom: 6px solid #ff7d94;
  border-radius: 4em;
}
</style>
